<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.works {
  width: 780px;
  min-width: 780px;
  max-width: 780px;
  float: right;
  margin-top: 15px;
}

li {
  position: relative;
  margin-bottom: 20px;
  height: 240px;
}

li a {
  display: block;
  overflow: hidden;
  border-radius: 10px;
  transform: translateZ(0);
}

a::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  left: 0;
  border-radius: 10px;
  /* background-color: rgba(0, 0, 0, 0.3); */
  background-image: -webkit-linear-gradient(-90deg, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.8) 100%);
